<template>
  <swiper class="swiper" :options="swiperOption">
    <swiper-slide>
      <slot></slot>
    </swiper-slide>
  </swiper>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
    name:'scroll',
    components:{
        Swiper,
        SwiperSlide
    },
    data(){
        return {
            swiperOption: {
                direction: 'vertical',
                slidesPerView: 'auto',
                setWrapperSize: true,
                freeMode: true,
                mousewheel: true,  
            }
        }
    },
    props: {
      data: {
        type: [Array, Object]
      }
    },
    watch:{
      data() {
        this.update();
      }
    },
     methods: {
      update() {
        this.$nextTick(() => {
          this.$refs.swiper && this.$refs.swiper.swiper.update();
          console.log('更新成功');
        });
      }
     }
}
</script>

<style lang="scss" scoped>
.swiper-container{
  height: 100%;
}
.swiper-slide{
  height: auto;
}
</style>


