<template>
  <div class="home">
    <div class="g-header-container header">
      <home-header></home-header>
    </div>
    <scroll class="scroll" ref="scroll"
    :data="recommends">
      <Myslider></Myslider>
      <mynav></mynav>
      <recommend @loaded="updateScroll"></recommend>
      <img src="./1.jpg" alt="" class='slider-img'>
      <img src="./1.jpg" alt="" class='slider-img'>
      <img src="./1.jpg" alt="" class='slider-img'>
      <img src="./1.jpg" alt="" class='slider-img'>
      <img src="./1.jpg" alt="" class='slider-img'>
      <img src="./1.jpg" alt="" class='slider-img'>
    </scroll>
    <router-view></router-view>
  </div>
  
</template>

<script>
import HomeHeader from './header'
import Myslider from './slider'
import scroll from 'base/scroll'
import mynav from './nav'
import recommend from './recommend'
export default {
  name: 'Home',
  data () {
    return {
      recommends:[],
    }
  },
  components:{
    HomeHeader,
    Myslider,
    scroll,
    mynav,
    recommend
  },
  methods:{
    updateScroll(data) {
      this.recommends = data
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~assets/scss/mixins";
  .home{
    height: 100%;
    background-color: $bgc-theme;
  }
  .router-link-active {
    color: $link-active-color;
  }
  .slider-img{
    width: 100%;
  }
</style>



